}

.registration__form__input .selectize-input {
  border: none;
  border-bottom: 1px solid #cecece;
  box-shadow: none !important;
  border-radius: 0;
  padding: 6px 0;
  width: 100%;
  margin-top: 5px;
}

.registration__form__input .selectize-input > input {
  display: none !important;
}

.registration__form__input .selectize-input > * {
  font-family: 'Eina03', sans-serif;
  color: #616161;
  font-size: 14px;
}

.registration__form__input .selectize-input:after {
  right: 2px !important;
}

.registration__form__input .selectize-dropdown [data-selectable] {
  color: #616161;
}

.registration__form__input .selectize-dropdown {
  border-radius: 3px;
  top: 37px !important;
}

.registration__form__input input {
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-size: 14px;
  font-weight: normal;
  color: #616161;
  padding: 10px 0;
  background: none;
}

.registration__form__input input:focus + label {
  opacity: 0;
}

.registration__form__input input:valid + label {
  opacity: 0;
}

.registration__form__input label {
  font-size: 14px;
  font-weight: normal;
  color: #616161;
  position: absolute;
  left: 0;
  bottom: 10px;
  transition: all 0.3s;
}

.registration__form__input label:before {
  content: '';
  position: absolute;
  right: -10px;
  top: 5px;
  width: 6px;
  height: 6px;
  background-color: #fd3e39;
  border-radius: 100%;
}

